import { Entity } from "../entity";
import { abs } from "../utils";
/**
 * XZ
 *
 * @export
 * @param {Entity} a A
 * @param {Entity} b B
 * @return {number} XZ
 */
export function manhattan_xz(a: Entity, b: Entity): number {
  return abs(a.position.x - b.position.x) + abs(a.position.z - b.position.z);
}

